import GetIcon from "./modules/GetIcon";
import styled from "styled-components";
import { MenuButton } from "./modules/Navigate";

// CSS
const MenuBox = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 0;
  padding: 10px 0;
  margin-bottom: 15px; 
  
`;

const MenuItem = styled.div`
  margin: 0 20px; /*       */
`;

export const MenuBar = () => {
    return (
        <MenuBox>
            <MenuItem>
                <MenuButton name={""} iconName={"home-white2.png"} dFlag={false} />
            </MenuItem>
            <MenuItem>
                <MenuButton name={""} iconName={"community-white2.png"} dFlag={true} path={"communicationMain"} />
            </MenuItem>
            <MenuItem>
                <MenuButton name={""} iconName={"chatbot-white7.png"} dFlag={true} path={"culi"} />
            </MenuItem>
            <MenuItem>
                <MenuButton name={""} iconName={"profile-white3.png"} dFlag={true} path={"myPage"} />
            </MenuItem>
        </MenuBox>
    );
};