import GetIcon from "./modules/GetIcon";
import styled from "styled-components";
import DaumPostcode from "react-daum-postcode";
import React, { useState, useEffect, useRef } from "react";
import { PWCheck, GetUserData, ProfileUpdate } from "./modules/DataRouter";
import "./style.css";
// css
const CenterBox = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
`;

const CuliImg = styled.img`
    height: 51px;
    left: 155px;
    object-fit: cover;
    top: 150px;
    width: 50px;
    margin-bottom: 7px;
`;
const CuliMsg = styled.div`
    height: 66px;
    left: 43px;
    top: 201px;
    width: 276px;
`;
const P = styled.p`
    white-space: pre-wrap;
    color: #8abfff;
    font-size: 16px;
    line-height: normal;
    position: absolute;
    text-align: center;
`;
const InputContainer = styled.div`
  width: 285px;
  height: 47px;
  border-radius: 6px;
  background: ${(props) => props.bg};
  margin-bottom: 10px;
  display: flex;
  align-items: center;
`;
const InputTag = styled.input`
  width: 210px;
  margin-left: 10px;
  border: none;
  background: ${(props) => props.bg};
  outline: none;
  color: #939393;
  ${props => props.cursor && `
    pointer-events: none;
    caret-color: transparent;
  `}
`
const InputImg = styled.img`
  width: 22px;
  margin-left: 20px;
  ${props => props.cursor && `
    pointer-events: none;
    caret-color: transparent;
  `}
`;
const Form = styled.form`
    margin-top: ${(props) => props.top};
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;  
`;
const InContainer = styled.div`
    width: 285px;
`;
const Text = styled.p`
  text-align: ${(props) => props.align};
  width:320px;
  color: ${(props) => props.color};
  font-size: ${(props) => props.size};
`;
const EmptyContainer = styled.div`
    margin-top: ${(props) => props.top};
    padding: 30px 0;
    background-color: #FAFAFA;
    width: 320px;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
`;
const Input = ({ icon, t, n, ph, v, onChange, readFlag, max  }) => {
  return (
    <InputContainer bg={readFlag === true ? "#FAFAFA" : "#EAE8E8"}>
      <InputImg cursor={readFlag} src={GetIcon(icon)}/>
      <InputTag type={t} cursor={readFlag} name={n} bg={readFlag === true ? "#FAFAFA" : "#EAE8E8"} value={v} placeholder={ph} onChange={onChange} readOnly={readFlag} maxLength={max} />
    </InputContainer>
  );
};
const Culi = () => {
    return (
        <CenterBox>
            <CuliImg src={GetIcon("chatbot-white7.png")} />
            <CuliMsg>
                <div className="overlap-group-wrapper">
                    <div className="overlap-group-2">
                        <img className="polygon" alt="Polygon" src={GetIcon("polygon.png")}/>
                        <div className="rectangle">
                            <P>{"  \n   !"}</P>
                        </div>
                    </div>
                </div>
            </CuliMsg>
        </CenterBox>
    )
}


export const UpdateProfile = () => {
    const [flag, setFlag] = useState(false);
    const [msg, setMsg] = useState();
    const [openPostcode, setOpenPostcode] = useState(false);
    const [secFlag, setSecFlag] = useState(false);
    const [sexImgName, setSexImgName] = useState();

    const [formValues, setFormValues] = useState({
        id: null,
        pw: null,
        name: null,
        nickName: null,
        address: null,
        postNum: null,
        sex: null,
        phoneNum: null,
        createDate: null
    });
    //   
    const kakaoHandler = {
        //  
        clickButton: () => {
            setOpenPostcode(true);
        },
        //  
        selectAddress: (data) => {
            var addr = "";

            if (data.userSelectedType === "R") {
                addr = data.roadAddress;
            } else {
                addr = data.jibunAddress;
            }
            setOpenPostcode(false);

            formValues.address = addr;
            formValues.postNum = data.zonecode;
            console.log(formValues);
        },
    };
    const handleChange = (event) => {
        const { name, value } = event.target;
        setFormValues(prevState => ({
            ...prevState,
            [name]: value
        }));
    };
    const InfoOn = async (event) => {
        const result = await PWCheck(event);
        if(result === false) setMsg("   .");
        else setMsg("");
        
        const userData = await GetUserData(event);
        setFormValues(userData);

        const sexImgName = formValues.sex === "M" ? "man.png" : "woman.png";
        setSexImgName(sexImgName);
        setFlag(result);
    }
    const InfoUpdate = async (event) => {
        event.preventDefault();
        const result = await PWCheck(event);
        if(result === false) setMsg("   .");
        else setMsg("");

        if(formValues.pw === event.target.pw.value){

            ProfileUpdate(formValues);
            console.log(" ");
        }
        else{
            console.log(" ");
        }
        
    }

    return (
        <div className="main">
            <div className="div">
                <CenterBox>
                    {!flag ? (
                        <div className="nanda">
                            <Culi/>
                            <Form onSubmit={InfoOn} method="post" top="50px">
                                <EmptyContainer top="30px">
                                    <InContainer>
                                        <Input icon={"closed-padlock-gray.png"} t={"password"} n={"pw"} ph={""}/>
                                        <span style={{color:"#FF8E8F", fontSize: "12px", marginLeft: "5px"}}>{msg}</span>
                                        <input className="loginSubmit" type="submit" value={""}/>
                                    </InContainer>
                                </EmptyContainer>
                                
                            </Form>
                        </div>
                    ) : (
                        //    
                        <Form onSubmit={InfoUpdate} method="post" top="10px">
                            {!secFlag && (
                                <div>
                                    <Text color={"#3252C2"} size={"15px"} style={{marginTop:"30px"}}><span style={{fontWeight:"bold"}}>CULISO</span> <span style={{color:"#4B66C8"}}>Account</span></Text>
                                    <EmptyContainer>
                                        <Input icon={sexImgName} t={"text"} v={formValues.id} n={"id"} ph={""} onChange={handleChange} readFlag={true}/>
                                        <Input icon={"phone-gray.png"} t={"text"} v={formValues.phoneNum} n={"phoneNum"} ph={""} onChange={handleChange} readFlag={true} />
                                        <Input icon={"calendar-gray.png"} t={"text"} v={formValues.createDate} n={"createDate"} ph={""} onChange={handleChange} readFlag={true} />
                                        <br/>
                                        <Input icon={"N-gray.png"} t={"text"} v={formValues.name} n={"name"} ph={""} onChange={handleChange} readFlag={false} />
                                        <Input icon={"N-gray.png"} t={"text"} v={formValues.nickName} n={"nickName"} ph={""} onChange={handleChange} readFlag={false} />
                                        <br/>
                                        <Input icon={"location-gray.png"} t={"text"} v={formValues.address} n={"address"} ph={""} onChange={handleChange} readFlag={false} />
                                        <div className="inputBG">
                                            <img class="inputImg" src="/location-gray.png" alt="" style={{marginLeft: "20px"}}/>
                                            <input class="inputText" type="text" name="postNum" placeholder="" maxlength="5" id="iPostNum" value={formValues.postNum}/>
                                            <input class="postBG" type="button" value=" " onClick={kakaoHandler.clickButton}/>
                                        </div>
                                        {openPostcode && (
                                            <DaumPostcode
                                            style={{
                                                width: "100%", //     
                                                height: "100%", //     
                                                position: "fixed", //     
                                                top: 0, //      
                                                left: 0, //     
                                                zIndex: 9999, //      
                                            }}
                                            onComplete={kakaoHandler.selectAddress} //     
                                            autoClose={false}
                                            defaultQuery=""
                                            />
                                        )}
                                        <input className="loginSubmit" type="button" value={""} onClick={()=>{setSecFlag(true)}}/>
                                    </EmptyContainer>
                                </div>
                            )}
                            {secFlag && (
                                <EmptyContainer top="270px">
                                    <Culi/>
                                    <InContainer>
                                        <Input icon={"closed-padlock-gray.png"} t={"password"} n={"pw"} ph={""} />
                                        <span style={{color:"#FF8E8F", fontSize: "12px", marginLeft: "5px"}}>{msg}</span>
                                        <input className="loginSubmit" type="submit" value={""} />
                                    </InContainer>
                                </EmptyContainer>
                            )}
                        </Form>
                    )}
                </CenterBox>
            </div>
        </div>
    );
};
  

