
import { MenuBar } from "./MenuBar";
import GetIcon from "./modules/GetIcon";
import { Cookies } from "react-cookie";
import styled from "styled-components";
import "./style.css";
import { useState, useEffect } from "react";
import { GetAddress } from "./modules/DataRouter";
import { Geolocation } from '@capacitor/geolocation';
import { AfterDeviceMain } from './AfterDeviceMain';

// css
const CenterBox = styled.div`
  display: flex;
  justify-content: ${(props) => props.align};
  align-items: center;
  margin-top: ${(props) => props.top};
`;

const Text = styled.span`
  text-align: ${(props) => props.align};
  color: ${(props) => props.color};
  font-size: ${(props) => props.size};
  font-family: ${(props) => props.font};
`;

const Img = styled.img`
  width: ${(props) => props.width};
  margin-top: ${(props) => props.top};
  color: ${(props) => props.color};
`;

const Label = styled.label`
  display: flex;
  align-items: center;
  margin-left: 20px;
`;

const RightContainer = styled.div`
  display: flex;
  align-items: center;
  margin-right: 20px;
`;
const EmptyContainer = styled.div`
  width: 300px;
  height: 295px;
  border-radius: 20px;
  background: #fafafa;
`;
const Button = styled.input`
  width: 129px;
  height: 35px;
  background-color: #b1dbfa;
  border: none;
  font-family: normal;
  font-weight: 700;
  margin-top: 35px;
  border-radius: 8px;
  font-size: 16px;
`;
const cookies = new Cookies();




function LogOut() {
  cookies.remove("token");
  alert("    !");
  window.location.href = "/login";
}
export const AfterMain = () => {                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                           
  const [address, setAddress] = useState();
  const [flag, setFlag] = useState(true);
  const [bluetoothFlag, setBluetoothFlag] = useState(false);

  useEffect(() => {
    //   
    const GetAddr = async () => {
      const address = await GetAddress();
      setAddress(address);
      console.log(": " + address);
    };

    GetAddr();
  }, []);

  useEffect(() => {
    //requestPermissions();
    if(flag){
      CheckPermissions();
      setFlag(false);
    }
  }, []);

  //    
  const CheckPermissions = async () => {
    try {
      //    
      const microphonePermission = await checkMicrophonePermission();
      const locationPermission = await checkLocationPermission();

      //     
      if (!microphonePermission) {
        await RequestMicrophonePermission();
      }

      if (!locationPermission) {
        await RequestLocationPermission();
      }
    } catch (error) {
      console.error('      :', error);
    }
  };
  //   
  const RequestMicrophonePermission = async () => {
    try {
      const status = await Permissions.request({ name: 'microphone' });
      if (status.state === 'granted') {
        console.log('  .');
      } else {
        console.log('  .');
      }
    } catch (error) {
      console.error('     :', error);
    }
  };

  //   
  const RequestLocationPermission = async () => {
    try {
      const coordinates = await Geolocation.getCurrentPosition();
      console.log('Current position:', coordinates);
      return coordinates;
    } catch (error) {
      console.error('     :', error);
      return false;
    }
  };
  //  
  const requestBlueTooth = async () => {
    try{
      if (!navigator.bluetooth) {
        console.log(" Web Bluetooth API  .");
      }

      const device = await navigator.bluetooth.requestDevice({
        acceptAllDevices: true
      });
      if(device){
        console.log('  :', device);
        // GATT  
        const server = await device.gatt.connect();
        // GATT    
        const services = await server.getPrimaryServices();
        //     
        for (const service of services) {
          const characteristics = await service.getCharacteristics();
          for (const characteristic of characteristics) {
              console.log(' :', characteristic.uuid);
              //         .
          }
        }
        // //  
        // const dataToWrite = new Uint8Array([97]);
        setBluetoothFlag(true);
      }
      //     
    } catch (error) {
      console.error('     :', error);
    }
  }

  //   
  const checkMicrophonePermission = async () => {
    try {
      const status = await Permissions.query({ name: 'microphone' });
      if (status.state === 'granted') {
        console.log('   .');
        return true;
      } else {
        console.log('   .');
        return false;
      }
    } catch (error) {
      console.error('     :', error);
      return false;
    }
  };

  //   
  const checkLocationPermission = async () => {
    try {
      const status = await Geolocation.checkPermissions();
      if (status.location !== 'granted') {
        const permission = await Geolocation.requestPermissions();
        if (permission.location !== 'granted') {
          throw new Error('Location permission not granted');
        }
        else{
          console.log("   .");
        }
        return true;
      } else {
        console.log('    .');
        return false;
      }
    } catch (error) {
      console.error('      :', error);
      return false;
    }
  };
  
  return (
    <div>
      {bluetoothFlag ? (
        <AfterDeviceMain/>
      ) : (
        <div className="afterMain">
          <div className="afterMainDiv">
            <CenterBox align="space-between" top="15px">
              <Label>
                <Text
                  size="12px"
                  style={{
                    whiteSpace: "nowrap",
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    maxWidth: "100px",
                  }}
                >
                  {address}
                </Text>
                <Img
                  src={GetIcon("mypage-modify.png")}
                  width={"13px"}
                  style={{ marginLeft: "5px" }}
                />
              </Label>
              <RightContainer>
                <Img src={GetIcon("logout.png")} width={"17px"} onClick={LogOut} />
                <Img
                  src={GetIcon("inform-black.png")}
                  width={"15px"}
                  style={{ marginLeft: "20px" }}
                />
                <Img
                  src={GetIcon("dropdown.png")}
                  width={"15px"}
                  style={{ marginLeft: "20px" }}
                />
              </RightContainer>
            </CenterBox>
            
            <CenterBox align="center" style={{width:"100vw", height:"100vh"}}>
              <EmptyContainer style={{marginBottom:"35.28px"}}>
                <Img src={GetIcon("home.png")} width={"134px"} top={"25px"} />
                <br />
                <br />
                <br />
                <Text size="20px" font="SejonghospitalBold">
                    ?
                </Text>
                <Button type="button" value={""} onClick={() => requestBlueTooth()} />
              </EmptyContainer>
            </CenterBox>
            {/*  */}
            <MenuBar/>
          </div>
        </div>
      )}
    </div>
    
  );
};
