import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import { Cookies } from "react-cookie";
import './App.css';
import GetIcon from './modules/GetIcon';
import { GetChatLog } from './modules/DataRouter';
import { BackButton } from "./modules/Navigate";
import { Geolocation } from '@capacitor/geolocation';
const cookies = new Cookies();

export function AI() {
    const [message, setMessage] = useState('');
    const [chatLog, setChatLog] = useState([]);
    const [listening, setListening] = useState(false);
    const [typingMessage, setTypingMessage] = useState(' ');
    const typingIntervalRef = useRef(null);
    const recognitionRef = useRef(null);
    const [flag, setFlag] = useState(true);

    useEffect(() => {
        if (!('webkitSpeechRecognition' in window)) {
            alert('     .   .');
        }
    }, []);

    useEffect(() => {
        return () => {
            if (typingIntervalRef.current) {
                clearInterval(typingIntervalRef.current);
            }
        };
    }, []);

    useEffect(() => {
        const chatLogElement = document.querySelector('.chat-log');
        chatLogElement.scrollTop = chatLogElement.scrollHeight;

        let didCancel = false;

        const fetchChatLog = async () => {
            try {
                const data = await GetChatLog();
                console.log(data);
                if (!didCancel) {
                    for (let i = 0; i < data.length; i++) {
                        updateChatLog({ sender: data[i].senderType === 'U' ? 'user' : 'bot', text: data[i].userChatContext });
                    }
                }
            } catch (error) {
                if (!didCancel) {
                    console.error('Failed to fetch chat log:', error);
                }
            }
        };

        fetchChatLog();

        return () => {
            didCancel = true;
        };
    }, []);
    useEffect(() => {
        //requestPermissions();
        if(flag){
          CheckPermissions();
          setFlag(false);
        }
      }, []);
    
    //    
    const CheckPermissions = async () => {
        try {
            //    
            const microphonePermission = await checkMicrophonePermission();
            const locationPermission = await checkLocationPermission();

            //     
            if (!microphonePermission) {
            await RequestMicrophonePermission();
            }

            if (!locationPermission) {
            await RequestLocationPermission();
            }
        } catch (error) {
            console.error('      :', error);
        }
    };
    //   
    const RequestMicrophonePermission = async () => {
    try {
        const status = await Permissions.request({ name: 'microphone' });
        if (status.granted) {
        console.log('  .');
        } else {
        console.log('  .');
        }
    } catch (error) {
        console.error('     :', error);
    }
    };

    //   
    const RequestLocationPermission = async () => {
    try {
        const position = await Geolocation.getCurrentPosition();
        console.log(' :', position);
        return true;
    } catch (error) {
        console.error('     :', error);
        return false;
    }
    };
    //   
    const checkMicrophonePermission = async () => {
    try {
        const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
        if (stream) {
        console.log('   .');
        return true;
        } else {
        console.log('   .');
        return false;
        }
    } catch (error) {
        console.error('     :', error);
        return false;
    }
    };

    //   
    const checkLocationPermission = async () => {
    try {
        const status = await Geolocation.checkPermissions();
        if (status.location === 'granted') {
        console.log('    .');
        return true;
        } else {
        console.log('    .');
        return false;
        }
    } catch (error) {
        console.error('      :', error);
        return false;
    }
    };
    const requestMicrophonePermission = async () => {
        try {
            await navigator.mediaDevices.getUserMedia({ audio: true });
            console.log('  .');
        } catch (error) {
            console.error('     :', error);
        }
    };

    const startListening = async () => {
        if (!('webkitSpeechRecognition' in window)) return;

        await requestMicrophonePermission();

        const recognition = new window.webkitSpeechRecognition();
        recognitionRef.current = recognition;
        recognition.lang = 'ko-KR';
        recognition.continuous = false;
        recognition.interimResults = false;

        recognition.onstart = () => {
            setListening(true);
            updateChatLog({ sender: 'system', text: '  ...' });
        };

        recognition.onresult = (event) => {
        const transcript = event.results[0][0].transcript;
            setMessage(transcript);
            sendMessage(transcript);
            setListening(false);
        };

        recognition.onerror = (event) => {
            console.error('  :', event.error);
            setListening(false);
        };

        recognition.onend = () => {
            setListening(false);
            setChatLog((prevChatLog) => prevChatLog.filter((message) => message.text !== '  ...'));
        };

        recognition.start();

    };

    const stopListening = () => {
        if (recognitionRef.current) {
            recognitionRef.current.stop();
        }
        setListening(false);
        stopListeningIndicator();
    };

    const getLocationPermission = () => {
        return new Promise((resolve, reject) => {
            if (navigator.geolocation) {
                navigator.geolocation.getCurrentPosition(
                    (position) => {
                        const { latitude, longitude } = position.coords;
                        console.log(' :', position);
                        resolve({ latitude, longitude });
                    },
                    (error) => {
                        console.error('     :', error);
                        reject(error);
                    },
                    {
                        enableHighAccuracy: true,
                        timeout: 5000,
                        maximumAge: 0
                    }
                );
            } else {
                console.error('     .');
                reject(new Error('     .'));
            }
        });
    };

    const getLocationAndSendMessage = async (msg) => {
        try {
            const position = await getLocationPermission();
            sendMessageWithLocation(msg, position.latitude, position.longitude);
        } catch (error) {
            console.error('    :', error);
            sendMessage(msg);
        }
    };

    const sendMessageWithLocation = async (msg, latitude, longitude) => {
        if (!msg.trim()) return;

        updateChatLog({ sender: 'user', text: msg });

        try {
            startTypingIndicator();

            const token = cookies.get("token");

            if (!token) {
                console.log(" ");
                return false; //    false 
            }

            const response = await axios.post('https://culiso.duckdns.org/chat', { message: msg, latitude, longitude }, { headers: { "Authorization": `Bearer ${token}` } });
            stopTypingIndicator();
            handleBotResponse(response.data.response);
        } catch (error) {
            stopTypingIndicator();
            console.error('       :', error);
            alert('       .');
        }
    };

    const sendMessage = async (msg) => {
        if (!msg.trim()) return;

        updateChatLog({ sender: 'user', text: msg });

        try {
            startTypingIndicator();

            const token = cookies.get("token");

            if (!token) {
                console.log(" ");
                return false; //    false 
            }

            const response = await axios.post('https://culiso.duckdns.org/chat', { message: msg }, { headers: { "Authorization": `Bearer ${token}` } });
            stopTypingIndicator();
            handleBotResponse(response.data.response);
        } catch (error) {
            stopTypingIndicator();
            console.error('    :', error);
            alert('    .');
        }
    };

    const handleBotResponse = (response) => {
        const cleanedResponse = response.replace(/\*\*|###|####/g, ''); // '**', '###'  '####' 
        updateChatLog({ sender: 'bot', text: cleanedResponse });
    };

    const updateChatLog = (message) => {
        setChatLog((prevChatLog) => [...prevChatLog, message]);
        setMessage('');
    };

    const handleInputChange = (e) => {
        setMessage(e.target.value);
    };

    const handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            getLocationAndSendMessage(message);
        }
    };

    const formatMessage = (text) => {
        return text.split('\n').map((line, index) => {
            if (line.startsWith('  ')) { //    
                return <div key={index} className="formatted-line indented">{line}</div>;
            }
            return <div key={index} className="formatted-line">{line}</div>;
        });
    };

    const startTypingIndicator = () => {
        setTypingMessage(' ');
        typingIntervalRef.current = setInterval(() => {
            setTypingMessage(prev => prev.length < 9 ? prev + '.' : ' ');
        }, 500);

        updateChatLog({ sender: 'bot', text: ' ' });
    };

    const stopTypingIndicator = () => {
        if (typingIntervalRef.current) {
            clearInterval(typingIntervalRef.current);
            typingIntervalRef.current = null;
        }

        // Remove the typing indicator
        setChatLog((prevChatLog) => prevChatLog.filter((message, index) => index !== prevChatLog.length - 1));
    };

    const startListeningIndicator = () => {
        updateChatLog({ sender: 'system', text: '  ' });
    };

    const stopListeningIndicator = () => {
        setChatLog((prevChatLog) => prevChatLog.filter((message) => message.text !== '  '));
    };

    // const [value, setValue] = useState("");
    // const { listen, listening, stop } = useSpeechRecognition({
    //   onResult: result => {
    //     //   value 
    //     setValue(result);
    //     console.log("  -" + value);
    //   }
    // });
    return (
        <div className="ai">
            <div className='aiDiv'>
                <div className="App">
                    <div className='topMenubar'>
                        <BackButton left="20px"/>
                        <span style={{marginLeft: "10px"}}></span>
                    </div>
                    <div className="chat-log">
                        <div>
                            {chatLog.map((entry, index) => (
                                <div key={index}>
                                    {entry.sender === 'bot' && 
                                    <div className="culiBox">
                                        <img className="culi" src={GetIcon("chatbot-white7.png")} alt="bot" />
                                        <div><span></span></div>
                                    </div>}
                                    <div className={`chat-box ${entry.sender}`}>
                                        <div className={`chat-message ${entry.sender}`}>
                                            {entry.sender === 'bot' && entry.text === ' ' ? typingMessage : formatMessage(entry.text)}
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                    <div className='chat-box'>
                        <div className="chat-input">
                            <input
                                type="text"
                                value={message}
                                onChange={handleInputChange}
                                onKeyPress={handleKeyPress}
                                placeholder=" .."
                            />
                            <img src={GetIcon("send-blue.png")} style={{right: '0', marginRight: '60px'}} width="30px" onClick={() => getLocationAndSendMessage(message)} alt='' />
                            {listening ? (
                                <img 
                                    src={GetIcon("listening-blue.png")} 
                                    style={{right: '0', marginRight: '15px'}} 
                                    width="30px"
                                    alt='' 
                                    onClick={stopListening} 
                                />
                            ) : (
                                <img 
                                    src={GetIcon("voice-blue.png")} 
                                    style={{right: '0', marginRight: '15px'}} 
                                    width="30px"
                                    alt='' 
                                    // onMouseDown={startListening} 
                                    // onMouseUp={stopListening}
                                    onClick={startListening} 
                                />
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
